<template>
      <div ref="wrapper">
          <slot></slot>
      </div>
</template>
<script>
import BScroll from 'better-scroll'
  export default {
    props: {
      /**
       * 1 scroll
       * 2 scroll
       * 3 scrollswipescroll
       */
      probeType: {
        type: Number,
        default: 1
      },
      /**
       * click
       */
      click: {
        type: Boolean,
        default: true
      },
      /**
       * 
       */
      scrollX: {
        type: Boolean,
        default: false
      },
      /**
       * 
       */
      listenScroll: {
        type: Boolean,
        default: true
      },
      /**
       * 
       */
      pullup: {
        type: Boolean,
        default: false
      },
      /**
       * 
       */
      pulldown: {
        type: Boolean,
        default: false
      },
      /**
       * 
       */
      beforeScroll: {
        type: Boolean,
        default: false
      },
      /**
       * scroll
       */
      refreshDelay: {
        type: Number,
        default: 20
      },
      bounce:{
        type:Boolean,
        default:true
      }
    },
    mounted() {
      // DOMbetter-scroll
      setTimeout(() => {
        this._initScroll()
      }, 20)
    },
    methods: {
      _initScroll() {
        if (!this.$refs.wrapper) {
          return
        }
        // better-scroll
        this.scroll = new BScroll(this.$refs.wrapper, {
          probeType: this.probeType,
          click: this.click,
          scrollX: this.scrollX,
          pullUpLoad: {
            threshold: 50,
            moreTxt:"",
            noMoreTxt:""
          },
          bounce:this.bounce
        })
        // 
        if (this.listenScroll) {
          this.scroll.on('scroll', (pos) => {
              this.$emit('scroll',pos)
          })
        }
        // 
        if (this.pulldown) {
          this.scroll.on('touchend', (pos) => {
            // 
            if (pos.y > 50) {
              this.$emit('pulldown')
            }
          })
        }
        this.scroll.on("pullingUp",()=>{
          this.$emit("pullingUp")
        })
        // 
        if (this.beforeScroll) {
          this.scroll.on('beforeScrollStart', () => {
            this.$emit('beforeScroll')
          })
        }
      },
      disable() {
        // better-scrolldisable
        this.scroll && this.scroll.disable()
      },
      enable() {
        // better-scrollenable
        this.scroll && this.scroll.enable()
      },
      refresh() {
        // better-scrollrefresh
        this.scroll && this.scroll.refresh()
      },
      scrollTo() {
        // better-scrollscrollTo
        this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
      },
      scrollToElement() {
        // better-scrollscrollToElement
        this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
      },
      finishPullUp(){
        this.scroll && this.scroll.finishPullUp()
      },
      closePullUp(){
        this.scroll && this.scroll.closePullUp()
      },
      openPullUp(){
        this.scroll && this.scroll.openPullUp()
      },
      getY(){
        return this.scroll?.y
      }
    }
  } 
</script>

